<template>
  <v-expansion-panel class="panel-type-friends">
    <v-expansion-panel-header>{{ firstname }} {{ lastname }}</v-expansion-panel-header>
    <v-expansion-panel-content>
      <v-form @submit.prevent>
        <v-container>
          <v-text-field
            v-model.lazy="firstname"
            color="success"
            :rules="rules.required"
            counter
            :maxlength="25"
            :label="$vuetify.lang.t('$vuetify.form.firstname')"
            required
          />
          <v-text-field
            v-model.lazy="lastname"
            color="success"
            :rules="rules.required"
            counter
            :maxlength="25"
            :label="$vuetify.lang.t('$vuetify.form.lastname')"
            required
          />
          <v-text-field
            v-model.lazy="mail"
            color="success"
            :rules="rules.requiredMail"
            counter
            :maxlength="50"
            :label="$vuetify.lang.t('$vuetify.form.mail')"
            type="mail"
            required
          />

          <div class="d-flex flex-row mt-3">
            <div class="ml-auto"/>
            <v-btn text dense color="error" class="pa-0" @click="$emit('remove')"><v-icon>mdi-minus</v-icon> {{ $vuetify.lang.t('$vuetify.move.step3.btnRemove') }}</v-btn>
          </div>
        </v-container>
      </v-form>
    </v-expansion-panel-content>
  </v-expansion-panel>
</template>

<script>
import { recObjectByPath } from '@/helper/static'

export default {
  name: 'move-step3-panel-type-friends',
  props: {
    path: String,
    idx: Number
  },
  data: () => ({
    rules: {
      required: null,
      requiredMail: null
    }
  }),
  created() {
    this.rules.required = [
      v => !!v || this.$vuetify.lang.t('$vuetify.rules.required')
    ]
    this.rules.requiredMail = [
      v => !!v || this.$vuetify.lang.t('$vuetify.rules.required'),
      v => v.length <= 50 || ' ',
      v => /\S+@\S+\.\S+/.test(v) || this.$vuetify.lang.t('$vuetify.rules.invalidMail')
    ]
  },
  computed: {
    firstname: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).firstname
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.firstname`, value })
      }
    },
    lastname: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).lastname
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.lastname`, value })
      }
    },
    mail: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).mail
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.mail`, value })
      }
    }
  }
}
</script>

<style scoped>
.width-70vh {
  width: 70vh;
}

.width-50vh {
  width: calc(50% - 1rem);
}
</style>