<template>
  <v-expansion-panel class="panel-type-address">
    <v-expansion-panel-header>{{ use }} {{ name }}</v-expansion-panel-header>
    <v-expansion-panel-content>
      <v-form @submit.prevent>
        <v-container>
          <v-text-field
            v-model.lazy="use"
            color="success"
            :rules="rules.required"
            counter
            :maxlength="25"
            :label="labelUse"
            required
          />
          <v-text-field
            v-model.lazy="name"
            color="success"
            :rules="rules.required"
            counter
            :maxlength="25"
            :label="labelName"
            required
          />
          <div class="d-flex flex-row">
            <v-text-field
              class="mr-2 width-70vh"
              color="success"
              v-model.lazy="street"
              :rules="rules.required"
              counter
              :maxlength="25"
              :label="$vuetify.lang.t('$vuetify.form.street')"
              required
            />
            <v-text-field
              v-model.lazy="nr"
              color="success"
              :rules="rules.required"
              counter
              :maxlength="5"
              :label="$vuetify.lang.t('$vuetify.form.nr')"
              required
            />
          </div>
          <v-text-field
            v-model.lazy="additional"
            color="success"
            counter
            :maxlength="25"
            :label="$vuetify.lang.t('$vuetify.form.additional')"
          />
          <div class="d-flex flex-row">
            <v-text-field
              class="mr-2"
              v-model.lazy="postcode"
              color="success"
              :rules="rules.required"
              counter
              :maxlength="5"
              :label="$vuetify.lang.t('$vuetify.form.postcode')"
              required
            />
            <v-text-field
              class="width-70vh"
              v-model.lazy="location"
              color="success"
              :rules="rules.required"
              counter
              :maxlength="25"
              :label="$vuetify.lang.t('$vuetify.form.location')"
              required
            />
          </div>

          <div class="d-flex flex-row mt-3">
            <div class="ml-auto"/>
            <v-btn text dense color="error" class="pa-0" @click="$emit('remove')"><v-icon>mdi-minus</v-icon> {{ $vuetify.lang.t('$vuetify.move.step3.btnRemove') }}</v-btn>
          </div>
        </v-container>
      </v-form>
    </v-expansion-panel-content>
  </v-expansion-panel>
</template>

<script>
import { recObjectByPath } from '@/helper/static'

export default {
  name: 'move-step3-panel-type-address',
  props: {
    path: String,
    idx: Number,
    labelUse: String,
    labelName: String
  },
  data: () => ({
    rules: {
      required: null
    }
  }),
  created() {
    this.rules.required = [
      v => !!v || this.$vuetify.lang.t('$vuetify.rules.required')
    ]
  },
  computed: {
    use: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).use
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.use`, value })
      }
    },
    name: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).name
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.name`, value })
      }
    },
    street: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).street
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.street`, value })
      }
    },
    nr: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).nr
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.nr`, value })
      }
    },
    additional: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).additional
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.additional`, value })
      }
    },
    postcode: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).postcode
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.postcode`, value })
      }
    },
    location: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).location
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.location`, value })
      }
    }
  }
}
</script>

<style scoped>
.width-70vh {
  width: 70vh;
}

.width-50vh {
  width: calc(50% - 1rem);
}
</style>