<template>
  <div class="wrapped">
    <span>{{ $vuetify.lang.t('$vuetify.move.step1.headline') }}</span><br/>
    
    <div :class="{'d-flex': true, 'flex-row': !$vuetify.breakpoint.xsOnly, 'flex-column': $vuetify.breakpoint.xsOnly, 'full-width': true}">
      <div class="d-flex flex-column full-width">
        <Address
          class="ma-1 mt-3"
          :title="$vuetify.lang.t('$vuetify.move.step1.currentAddress.title')" 
          address="currentAddress"
          :showDates="true"
          :date-label="$vuetify.lang.t('$vuetify.move.step1.currentAddress.date')"
          :key-label="$vuetify.lang.t('$vuetify.move.step1.currentAddress.key')" />
        <v-checkbox color="success" dense hide-details v-model="billCurrentAddress" :label="$vuetify.lang.t('$vuetify.move.step1.checkboxes.currentAddress')" class="ml-2"/>
        <v-checkbox color="success" dense hide-details v-model="billOtherAddress" :label="$vuetify.lang.t('$vuetify.move.step1.checkboxes.otherAddress')" class="ml-2"/>
        <Address v-if="billOtherAddress"
          class="ma-1 mt-3 mb-3"
          :title="$vuetify.lang.t('$vuetify.move.step1.otherAddress.title')" 
          address="otherAddress"
          :showDates="false"/>
      </div>
      <div :class="{ 'ml-10': !$vuetify.breakpoint.xsOnly }">
        <Address
          class="ma-1 mt-3"
          :title="$vuetify.lang.t('$vuetify.move.step1.newAddress.title')" 
          address="newAddress"
          :showDates="true"
          :date-label="$vuetify.lang.t('$vuetify.move.step1.newAddress.date')"
          :key-label="$vuetify.lang.t('$vuetify.move.step1.newAddress.key')" />
        <v-checkbox color="success" dense hide-details v-model="billNewAddress" :label="$vuetify.lang.t('$vuetify.move.step1.checkboxes.newAddress')" class="ml-2"/>
      </div>
    </div>

    <NextPrevBar :show-next="true" :show-prev="false" />
  </div>
</template>

<script>
import Address from '@/components/move/step1/Address'

import NextPrevBar from '@/components/move/NextPrevBar'

export default {
  name: 'move-step1',
  components: {
    Address,
    NextPrevBar
  },
  data: () => ({
  }),
  computed: {
    billCurrentAddress: {
      get() {
        return this.$store.getters.billing.billCurrentAddress
      },
      set() {
        this.$store.commit('switchBillingAddress', 'billCurrentAddress')
      }
    },
    billNewAddress: {
      get() {
        return this.$store.getters.billing.billNewAddress
      },
      set() {
        this.$store.commit('switchBillingAddress', 'billNewAddress')
      }
    },
    billOtherAddress: {
      get() {
        return this.$store.getters.billing.billOtherAddress
      },
      set() {
        this.$store.commit('switchBillingAddress', 'billOtherAddress')
      }
    }
  }
}
</script>

