<template>
  <v-card class="home-person-card" :elevation="3">

    <v-form @submit.prevent>
      <v-container>
        <div class="d-flex flex-row mt-6 full-width">
          <v-text-field
            class="mr-2 width-50vh"
            color="success"
            v-model.lazy="salutation"
            :rules="rules.required"
            :label="$vuetify.lang.t('$vuetify.form.salutation')"
            type="text"
            counter
            :maxlength="25"
            required
          />
          <v-text-field
            class="width-50vh"
            color="success"
            v-model.lazy="title"
            :label="$vuetify.lang.t('$vuetify.form.title')"
            type="text"
            counter
            :maxlength="25"
          />
        </div>
        <v-text-field
          v-model.lazy="firstname"
          color="success"
          :rules="rules.required"
          counter
          :maxlength="25"
          :label="$vuetify.lang.t('$vuetify.form.firstname')"
          required
        />
        <v-text-field
          v-model.lazy="lastname"
          color="success"
          :rules="rules.required"
          counter
          :maxlength="25"
          :label="$vuetify.lang.t('$vuetify.form.lastname')"
          required
        />
        <div class="d-flex flex-row mt-6 full-width">
          <v-text-field
            class="mr-2 width-50vh"
            color="success"
            v-model.lazy="birthdate"
            :rules="rules.required"
            :label="$vuetify.lang.t('$vuetify.form.birthdate')"
            type="date"
            required
          />
          <div class="width-50vh"/>
        </div>
      </v-container>
    </v-form>

  </v-card>
</template>

<script>
import { recObjectByPath } from '@/helper/static'

export default {
  name: 'home-step2-person',
  props: {
    path: String
  },
  data: () => ({
    rules: {
      required: null
    }
  }),
  created() {
    this.rules.required = [
      v => !!v || this.$vuetify.lang.t('$vuetify.rules.required')
    ]
  },
  computed: {
    salutation: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).salutation
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.salutation`, value })
      }
    },
    title: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).title
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.title`, value })
      }
    },
    firstname: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).firstname
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.firstname`, value })
      }
    },
    lastname: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).lastname
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.lastname`, value })
      }
    },
    birthdate: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).birthdate
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.birthdate`, value })
      }
    }
  }
}
</script>

<style scoped>
.home-person-card {
  width: 95%;
  max-width: 350px;
}

.width-70vh {
  width: 70vh;
}

.width-50vh {
  width: calc(50% - 1rem);
}
</style>